import React from 'react';
import Song from './Song';


const Singles = ({ Items }) => {
    // if (true) {
    //     throw new Error('NO');
    // }
    return (
    <div className='singles' >
        {Items.map((album, i) => {
        return (
        <Song 
            key={Items[i].author.toString()} 
            reference={Items[i].reference} 
            title={Items[i].title} 
            author={Items[i].author} 
            year={Items[i].year}
            />
          );
        })
    }
    </div>  
    );
}


export default Singles;