import React from 'react';

const Song = (props) => {
    return (
    <div className='tc shadow-2 grow br3 near-black bg-lightest-blue pa3 ma2' >
        <img alt='album' 
        src={process.env.PUBLIC_URL + props.reference}  />
        <div className='o-90 system-sans-serif' >
            <h1>{ props.author }</h1>
            <h2>{ props.title }</h2>
            <p>{ props.year }</p>
        </div>
    </div>
    );
}


export default Song;